.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 10px;
    color: black;
}

.dialog {
    color: black;
}
.dialog img {
    width: 50px;
    height: 50px;
    border-radius: 30px;
}

.messages {
    padding: 10px;

}

.massage {
    color: black;
}

.dialogs .active {
    color: gold;
}



